$file-prefix: 'login-verifyCode';

.#{$file-prefix}-layout {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    
    .#{$file-prefix}-img {
        position: relative;
        top: -6px;
        width: 100px;
        height: 40px;
    }

    .#{$file-prefix}-input {
        background-color: transparent;
        outline: none;
        border: none;
        border-bottom: 1px solid #ccc;
        display: block;
        width: 100%;
        color: #000;
        padding-bottom: 10px;
        margin-bottom: 25px;
        margin-right: 20px;
    }

    .#{$file-prefix}-input::-webkit-input-placeholder {
        color: #ccc;
    }

    .#{$file-prefix}-input::-moz-placeholder {
        color: #ccc;
    }
}